.headerBox {
  height: 100vh;
}
.headerTitleSec {
  position: relative;
  background-color: var(--secondary-backcolor);
  /* margin: 5px; */
}
.floatLeft {
  /* position: absolute; */
  margin-top: 2em;
  text-align: left;
}
.header-name h1 {
  font-size: 2.5rem;
  font-weight: bolder;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}
.header-title {
  margin-top: 1rem;
  letter-spacing: 0.2ch;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1rem;
}
.cursor {
  animation: blink 1s ease-in-out infinite;
  font-size: 1rem;
  font-weight: bold;
  color: #c68e4a;
}
.cursor::before {
  content: "  ";
}
@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

nav {
  margin-top: 2rem;
}
nav a,
nav a:hover,
nav a:active {
  display: grid;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.2rem;
  margin-top: 1rem;
  color: #000;
}
nav a.active {
  background-color: #009688;
  color: #fff;
  height: 2rem;
  align-items: center;
  padding-left: 0.4rem;
}
/* ---- HEader section ends here */
.summaryBox {
  margin-top: 2em;
}
.summaryBox h4 {
  font-weight: bold;
  font-size: 2rem;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}
.summary-desc {
  text-align: justify;
  padding: 1rem;
  line-height: 1.3rem;
  letter-spacing: 0.1rem;
}

.sidePanelSec {
  height: 98vh;
  overflow: scroll;
}
.projectBox {
  margin-top: 2em;
}
.projectCards {
  padding: 10px;
  text-align: justify;
}
.projectCards a,
.projectCards a:hover {
  color: #000;
  text-decoration: none;
}

.geoBox {
  display: block;
  /* align-items: center;
  justify-content: center; */
  padding: 10px;
  margin: 20px 10px;
  border: 1px solid #dedede;
  float: left;
  clear: both;
}
.geoForm {
  display: block;
  border-bottom: 1px solid red;
  clear: both;
  padding: 10px;
}

.geoForm label {
  margin-right: 10px;
}
.geoBox .result {
  display: block;
  margin-top: 10px;
  padding: 10px;
  box-shadow: 0 0 2px #dedede;
}
.geoForm h6 {
  text-align: center;
}
.result div {
  display: block;
  width: 90%;
  height: 60px;
  border-bottom: 1px solid #dedede;
}
.loc-logo {
  float: left;
  width: 40px;
  height: 30px;
  color: gray;
}

/* Custom scroll bar */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #e7e9eb;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.NomatchBox,
.homeBox {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.NomatchBox h1 {
  font-size: 2rem;
  margin-left: 1rem;
}
.NomatchBox img {
  width: 60vw;
  height: 50vh;
}
.homeBox #Coderhead {
  animation: headMove 1s infinite ease-in-out alternate;
  transform-origin: bottom;
  transform-box: fill-box;
}

/* Animation Coder Head */
@keyframes headMove {
  from {
    transform: rotateZ(-3deg);
  }
  to {
    transform: rotateZ(3deg);
  }
}
.IntroText {
  padding: 2rem;
  font-size: 1.2rem;
}
.IntroText h1 {
  font-size: 3rem;
  font-weight: bolder;
}
.IntroText h1.greeting {
  color: #c68e4a;
}

/* -----Contact section starts  */
.contactBox {
  /* width: 100px; */
  margin-top: 2rem;
}
.contactBox a:first-child {
  margin-left: 0rem;
}
.contactBox a {
  margin-left: 0.5rem;
}
.contactBox a svg {
  color: #000;
  font-size: 2rem;
}
.contactBox a svg:hover {
  color: #009688;
}

.contact {
  width: 10rem;

  text-align: center;
  align-items: center;
  background: #3f3d56;
  border-radius: 20px;
  margin-top: 1rem;
}
.contact a {
  color: #fff;
  text-decoration: none;
  font-size: 1.5rem;
  position: relative;
  top: 60%;
  transform: translateY(-60%);
}
.contact a:hover,
.contact a:active {
  text-decoration: none;
  color: #fff;
}
/* Responsive code starts here */
@media only screen and (max-width: 600px) {
  #codeIllus {
    display: none;
  }
}
